.card__image-wrapper {
  position: relative;
  background-color: #B11C30;
  text-align: center; }
  .card__image-wrapper:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 75%; }
  .card__image-wrapper > .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }

.card__image {
  max-height: 80%;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

